import * as React from "react";
import {engine} from "../model/MyEngine";
import {Button} from "../overloaded/Button";

let textFile: any = null;
const makeTextFile = function (text: string) {
    const data = new Blob([text], {type: 'text/plain'});

    // If we are replacing a previously generated file we need to
    // manually revoke the object URL to avoid memory leaks.
    if (textFile !== null) {
        window.URL.revokeObjectURL(textFile);
    }

    textFile = window.URL.createObjectURL(data);

    // returns a URL you can use as a href
    return textFile;
};

const saveHandler = () => {
    const link = document.createElement('a');
    link.setAttribute('download', 'diagram.json');
    link.href = makeTextFile(JSON.stringify(engine.getModel().serialize()));
    document.body.appendChild(link);

    // wait for the link to be added to the document
    window.requestAnimationFrame(function () {
        const event = new MouseEvent('click');
        link.dispatchEvent(event);
        document.body.removeChild(link);
    });
};


export const SaveButton = () => {
    return (
    <>
        <Button variant="contained" color="primary" size="small" onClick={saveHandler}>
            Save
        </Button>
    </>
)};