import {NodeModel, NodeModelGenerics} from '@projectstorm/react-diagrams';
import {ProductPortModel} from './ProductPortModel';
import {getProductList, Product} from "../data/productList";
import {DeserializeEvent} from '@projectstorm/react-canvas-core';
import {get, omit} from 'lodash'

export const LabelPositions = {
    top:  'Top',
    right: 'Right',
    left: 'Left',
    bottom: 'Bottom',
    inside: 'Inside'
};


export class ProductNodeModel extends NodeModel<NodeModelGenerics> {
    productData: Product;
    constructor(productData: Product) {
        super({
            type: 'product',
        });
        this.productData = productData || {};
        this.productData.labelPosition || (this.productData.labelPosition = 'top');
        productData?.icon?.ports?.forEach(port => this.addPort(new ProductPortModel(port)));

    }

    setSelected(selected: boolean = true) {
        super.setSelected(selected);
        this.getParentCanvasModel().fireEvent({isSelected: selected, node: this}, 'selectionChanged');
    }

    serialize() {
        return {...super.serialize(), productData: omit(this.productData,  'icon') as Product}
    }

     deserialize(event: DeserializeEvent<this>) {
         event.data && super.deserialize(event);
         event.data && (this.productData = {...event.data.productData, ...get(getProductList(),  event.data.productData.id)});
     }

     updateProperty(propName: string, value: string) {
        this.productData[propName] = value;
        this.getParentCanvasModel().fireEvent({node: this, isCreated: false}, 'nodesUpdated');
     }

     getPropertyValue(propName: string) {
        return this.productData[propName];
     }

    remove() {
        super.remove();
        this.getParentCanvasModel().fireEvent({node: this}, 'nodeRemoved');
    }

}


