import * as React from 'react'
import {MyDiagramModel} from "../model/MyDiagramModel";
import {engine} from "../model/MyEngine";
import {Button} from "../overloaded/Button";

const loadHandler = () => {
    const input = document.createElement('input');
    input.setAttribute('type', 'file');
    document.body.appendChild(input);

    window.requestAnimationFrame(() => {
        const event = new MouseEvent('click');
        input.dispatchEvent(event);
        input.addEventListener('change', ev => {
            const file = ev && (ev.target as HTMLInputElement).files?.[0];
            const reader = new FileReader();
            reader.readAsText(file as Blob);
            reader.onloadend = ev => {
                const data = JSON.parse(ev?.target?.result as string);
                const model2 = new MyDiagramModel();
                model2.deserializeModel(data, engine);

                //HACK: hack to keep links from being selected on load
                //TODO: when new link model complete, see if this is needed anymore
                model2.getLinks().map(link => link.setSelected(false));


                engine.setModel(model2);
                engine.fireEvent({}, 'modelChanged')
            };
        });
        document.body.removeChild(input);
    });
};




export const LoadButton = () => {
    return (
        <Button variant="contained" color="primary" size="small" onClick={loadHandler}>Load</Button>
    )
};