import React from 'react'
import styled from "@emotion/styled";
import {getProductList, Product} from "../data/productList";
import {TrayItem} from "./TrayItem";
import {concat, get, map, update} from 'lodash'
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const TrayDiv = styled.div`
		min-width: 50px;
		background: rgb(20, 20, 20);
		flex-grow: 0;
		flex-shrink: 0;
		height: 100%;
		overflow: auto;
	`;


export const getComponents = () => Object.values(getProductList());

export const Tray = () => (
    <TrayDiv>
        <CategoryTree tree={organizeComponents()}/>
    </TrayDiv>
);

interface CategoryTreeProps {
    tree: Object
}

const CategoryTree: React.FC<CategoryTreeProps> = ({tree}) => (
    <div style={{padding: 5}}>
        {map(tree, (products, category) => (
            <ExpansionPanel key={category}>
                <ExpansionPanelSummary
                    expandIcon={<ExpandMoreIcon/>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography>{category}</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <ProductList products={products as any}/>


                </ExpansionPanelDetails>
            </ExpansionPanel>
        ))}
    </div>

);


interface ProductListProps {
    products: Array<Product>
}

const ProductList: React.FC<ProductListProps> = ({products}) => (
    <div style={{marginBottom: 5}}>
        {products.map(product => (
            <div key={product.id} style={{marginBottom: 8}}>
                <TrayItem model={product}/>
            </div>
        ))}
    </div>
);

const organizeComponents = () => {
    const tree = {};
    getComponents().forEach(c => {
        c.category !== 'Wire' && update(tree, `[${c.category}]`, () => concat(get(tree, `[${c.category}]`) || [], [c]));
    });
    return tree;
};


