import * as React from 'react';
import {LabelPositions, ProductNodeModel} from "../../model/ProductNodeModel";
import {TextField} from "../../overloaded/form/TextField";
import {ActionButtons} from "./ActionButtons";
import {SelectField} from "../../overloaded/form/SelectField";
import {AmazonTextAndImage} from "../../amazon/AmazonTextAndImage";
import {IfDefined} from "../../IfDefined";
import {Amazon} from "../../data/productList";
import {AmazonLearnMoreBtn} from "../../amazon/AmazonLearnMoreBtn";

interface NodePropertiesProps {
    node: ProductNodeModel
}

export const ProductNodeProperties = ({node}: NodePropertiesProps) => (
    <div key={node.getOptions().id}>
        <ActionButtons node={node}/>
        <TextField label="Label" defaultValue={node.getPropertyValue('label')} onChange={(ev) => node.updateProperty('label', ev.target.value)}/>
        <span style={{paddingLeft: 20}}>
            <SelectField inline defaultValue={node.getPropertyValue('labelPosition')} onChange={(ev) => node.updateProperty('labelPosition', ev.target.value)} options={LabelPositions} />
        </span>
        <div style={{padding: 5}}>
            {node.productData.brand && <div style={{fontSize: '1.5em'}}>{node.productData.brand}</div>}
            {node.productData.description && <div style={{fontSize: '1.2em'}}>{node.productData.description}</div>}
            {node.productData.modelNo && <div>Model: {node.productData.modelNo}</div>}
            <IfDefined value={node.productData.amazon}>
                {(amazon: Amazon) => (
                    <AmazonLearnMoreBtn amazon={amazon}/>
                )}
            </IfDefined>
        </div>
    </div>
);



