import * as React from 'react';
import {ProductNodeModel} from '../model/ProductNodeModel';
import {DiagramEngine} from '@projectstorm/react-diagrams';
import {ProductPortModel} from "../model/ProductPortModel";
import {Port} from "./Port";

interface ComponentNodeWidgetProps {
    node: ProductNodeModel;
    engine: DiagramEngine;
}

/**
 * @author Dylan Vorster
 */
export class ProductNodeWidget extends React.Component<ComponentNodeWidgetProps> {
    render() {
        return (
            <div
                style={{
                    position: 'relative',
                    width: this.props.node.productData?.icon?.width,
                    height: this.props.node.productData?.icon?.height,
                    border: this.props.node.isSelected() ? '1px solid white' : 'none',
                    borderRadius: 5
                }}>
                <img style={{ WebkitUserDrag: 'none', position: 'absolute'} as React.CSSProperties} src={this.props.node.productData?.icon?.img} height={this.props.node.productData?.icon?.height} width={this.props.node.productData?.icon?.width} alt={this.props.node.productData.name}/>
                <label style={{position: 'absolute', color: '#ddd', textAlign: 'center', display: 'block', ...getLabelPosition(this.props.node.productData)}}>{this.props.node.getPropertyValue('label')}</label>
                {Object.values(this.props.node.getPorts()).map(port => <Port key={port.getName()} port={port as ProductPortModel} />)}
            </div>
        );
    }
}

const getLabelPosition = (productData: any) => {
    const fns = {
        right: () => ({left: productData.icon.width + 5, lineHeight: `${productData.icon.height}px`, whiteSpace: 'nowrap'}),
        left: () => ({right: productData.icon.width + 5, lineHeight: `${productData.icon.height}px`, whiteSpace: 'nowrap'}),
        top: () => ({right: productData.icon.width / 2 - 50, top: -18, width: 100, textAlign: 'center'}),
        bottom: () => ({right: productData.icon.width / 2 - 50, bottom: -18, width: 100, textAlign: 'center'}),
        inside: () => ({top: 10, width: '100%', color: 'black'})
    } as any;
    return fns[productData.labelPosition]()
};
