import * as React from 'react';
import {useEffect, useState} from 'react';
import {PropertiesPanel} from "./propertiesPanel/PropertiesPanel";
import {ProductOrderPanel} from "./productOrder/ProductOrderPanel";
import {engine} from "../model/MyEngine";
import {useForceRender} from "../useForceRender";

export const Sidebar: React.FC = () => {
    const [node, setNode] = useState(null);
    const forceUpdate = useForceRender();

    useEffect(() => {
            const listener = engine.registerListener({
                modelChanged: () => {
                    setNode(null);
                    forceUpdate();
                }
            });
            return () => listener.deregister()
        }
    );

    useEffect(() => {
        const listener = engine.getModel().registerListener({
            selectionChanged: (data: any) => setNode(data.isSelected ? (data).node : null),
            nodeRemoved: ({node:n} : any) => n === node && setNode(null),
            linkRemoved: ({link:l} : any) => l === node && setNode(null)
        });
        return () => listener.deregister();
    });


    return node ? (
        <PropertiesPanel node={node as any}/>
    ) : (
        <ProductOrderPanel/>
    )
};